.page-header{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1.5rem;
}
.page-title{
  font-size: 1.5rem;
  font-weight: bold;
}
.page-subtitle{
  font-size: 0.875rem;
  color: var(--text-muted);
}
.page-actions{
  display: flex;
  gap: 0.5rem;
  margin-top: 1rem;
}